let map = document.querySelector("#map")
if(map){
    ymaps.ready(init);
}

function init() {
    let map,
        points = [
            {
                coords: [55.803939, 49.117212],
                title: 'Магазин',
                content: 'Описание',
                markerIcon: 'img/marker.svg'
            },
            {
                coords: [55.784784, 49.107941],
                title: 'Магазин',
                content: 'Описание',
                markerIcon: 'img/marker2.svg'
            },
            {
                coords: [55.778780, 49.226519],
                title: 'Магазин',
                content: 'Описание',
                markerIcon: 'img/marker3.svg'
            },


            {
                coords: [55.755246, 37.617779],
                title: 'Магазин',
                content: 'Описание',
                markerIcon: 'img/marker.svg'
            },
            {
                coords: [55.753154, 37.594045],
                title: 'Магазин',
                content: 'Описание',
                markerIcon: 'img/marker2.svg'
            }
        ];
    map = new ymaps.Map("map", {
        center: [55.803939, 49.117212],
        zoom: 4
    });

    points.forEach(function (point) {
        let placemark = new ymaps.Placemark(point.coords, {
            hintContent: point.title,
            balloonContentHeader: point.title,
            balloonContent: point.content
        }, {
            iconLayout: 'default#image',
            iconImageHref: point.markerIcon,
            hideIconOnBalloonOpen: false,
            iconImageSize: [46, 58],
            iconImageOffset: [-23, -29]
        });
        map.geoObjects.add(placemark);
    })

    $("._point-select").change(function () {
        let selectedPoint  = +$(this).find("option:selected").attr("data-point")
        $(this).addClass("_active")

        console.log(selectedPoint)
        map.setCenter(points[selectedPoint].coords);
        map.setZoom(16);
    })

    $("._city-select").change(function () {
        let selectedCity  = $(this).find("option:selected").attr("data-city")
        $(this).addClass("_active")

        console.log(selectedCity)
        $(".shops-nav__variants").removeClass("_active")
        $(`.shops-nav__variants.${selectedCity}`).addClass("_active")
    })
}
